import { Parallax } from "react-parallax";
import React from "react";
import "../parallax.css";
import "../App.css";

import bg from "../static/img/bg2.png";
import flanc1 from "../static/img/flanc1-new.png";
import flanc2 from "../static/img/flanc2.png";

import Timer from "./clock.js";
import { Link } from "react-scroll";

import bgimg from "../static/img/bgimgmobile.JPG";

export default class parallax extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      guestList: [],
      loading: true,
      activateArrow: "arrowdiv",
      mouseonArr: false,
    };
    this.hoverArr = this.hoverArr.bind(this);
  }
  hoverArr() {
    if (this.state.mouseonArr === false) {
      this.setState({
      activateArrow: "arrowdiv arrowfx",
      mouseonArr: true,
    })
    }
    else {
      this.setState({
        activateArrow: "arrowdiv",
        mouseonArr: false,
      })
    }
  }
  render() {
    return(
      <div className="">
        <div className="mobile-screen-only">
        <div className="text-oncarousel">
                  <div className="benetcam">BENJAMIN ET CAMILLE</div>
                  <p></p>
                  CRANS-MONTANA
                  <p>18 et 19 MARS 2022</p>
                  <div className="space-header-timer"></div>
                  <Timer></Timer>
                  <div className="space-timer-arrow"></div>
                    <Link
                      onMouseOver={this.hoverArr}
                      onMouseOut={this.hoverArr}
                      className="go-to-programme-link"
                      to="programme"
                      spy={true}
                      smooth={true}
                    >
                      <div className={this.state.activateArrow}>
                        <i className="fa fa-chevron-down" aria-hidden="true"></i>
                      </div>
                      
                    </Link>
            </div>
          <div className="div-for-parallax">
            <img className="img-prop" src={bgimg} alt={bgimg}></img>
          </div>
        </div>
        <div className="lg-screen-only">
          <Parallax
          bgImage={bg}
          className="mainparaimg"
          strength={300}
          renderLayer={(percentage) => (
            <div className="div-for-parallax">
              <img src={flanc1} alt={flanc1} className="style-flanc1"/>
              <img src={flanc2} alt={flanc2} className="style-flanc2"/>
                <div className="text-oncarousel">
                  <div className="benetcam">BENJAMIN ET CAMILLE</div>
                  <p></p>
                  CRANS-MONTANA
                  <p>18 et 19 MARS 2022</p>
                  <Timer></Timer>
                  <div className="space-div"></div>
                    <Link
                      onMouseOver={this.hoverArr}
                      onMouseOut={this.hoverArr}
                      className="go-to-programme-link"
                      to="programme"
                      spy={true}
                      smooth={true}
                    >
                      <div className={this.state.activateArrow}>
                        <i className="fa fa-chevron-down" aria-hidden="true"></i>
                      </div>
                      
                    </Link>
                </div>
            </div>
          )}
          >
          </Parallax>
        </div>
    </div>
    )
  }
}