import React from "react";
import "./../App.css";
import "./../Cards.css";

import five from "../static/img/guarda.jpg";
import four from "../static/img/etrier.jpg";
import three from "../static/img/3stars.jpg";
import flocon from "../static/img/flocon2.png";

export default class Cards extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      raclettefx: "bgimg",
      raclettebool: false,
      ceremofx: "bgimg",
      ceremobool: false,
      soireefx: "bgimg",
      soireebool: false,
      rowDisplay : "row normal-display",
      racletteShow: false,
      racletteStat: "raclette-content raclette-hide",
      racletteTitle : "bgbanner-text",
      racletteBtn: "",
      racletteBtnTitle: "AFFICHER",
      racletteBg: "",
    };
    this.overRaclette = this.overRaclette.bind(this);
    this.overCeremo = this.overCeremo.bind(this);
    this.overSoiree = this.overSoiree.bind(this);
    this.showInfoRaclette = this.showInfoRaclette.bind(this);
    this.showInfoCere = this.showInfoCere.bind(this);
    this.showInfoSoiree = this.showInfoSoiree.bind(this);
  }
  overRaclette() {
    var val1 = "";
    var val2 = "";
    // if it is hover
    if (this.state.raclettebool === false) {
      // create hover effect
      val1 = "bgimg active";
      val2 = true;
    }
    // if mouse leaves
    else {
      // remove fx
      val1 = "bgimg";
      val2 = false;
      // if raclette is show on then
      console.log(this.state.racletteShow);
    }
    this.setState({
      raclettefx: val1,
      raclettebool: val2,
    });
    if (val2 === true && this.state.racletteShow === true) {
      this.setState({
        racletteBg: ""
      });
    }
    else if (val2 === false && this.state.racletteShow === false) {
      this.setState({
        racletteBg: ""
      });
    }
    else if ((val2 === false && this.state.racletteShow === true)){
      this.setState({
        racletteBg: "active"
      });
    }
  }
  overCeremo() {
    var val1 = ""
    var val2 = ""
    if (this.state.ceremobool === false) {
      val1 = "bgimg active";
      val2 = true;
    }
    else {
      val1 = "bgimg"
      val2 = false
    }
    this.setState({
      ceremofx: val1,
      ceremobool: val2,
    });
  }
  overSoiree() {
    var val1 = ""
    var val2 = ""
    if (this.state.soireebool === false) {
      val1 = "bgimg active";
      val2 = true;
    }
    else {
      val1 = "bgimg"
      val2 = false
    }
    this.setState({
      soireefx: val1,
      soireebool: val2,
    });
  }
  showInfoRaclette() {
    if (this.state.racletteShow === false) {
      this.setState({
        racletteShow: true,
        racletteTitle: "bgbanner-text rmoveup",
        racletteBtn: "rmovedown",
        racletteBtnTitle: "FERMER",
      })
      setTimeout(() => { this.setState({
        racletteStat: "raclette-content raclette-show",
      }) }, 500);
    }
    else {
      this.setState({
        racletteShow: false,
        racletteStat: "raclette-content raclette-hide",
        racletteTitle: "bgbanner-text",
        racletteBtn: "",
        racletteBtnTitle: "AFFICHER",
      })
    }
    

  }
  showInfoCere() {
    console.log("cérémonie")
  }
  showInfoSoiree() {
    console.log("diner")
  }
  render() {
    return (
      <div className="row">
        <div className="col-sm wedding-card mr-4 mb-4">
          <div class="centerBox" onMouseOver={this.overRaclette} onClick={this.showInfoRaclette} onMouseOut={this.overRaclette}>
            <img className={`${this.state.raclettefx} ${this.state.racletteBg}`}
            src={five} 
            alt={five}></img>
            <div class="categoryWrapper">
            <div className={`${this.state.racletteStat}`}>
              <div className="date-title"><span className="link-hotel"><a href="https://pasdelours.ch/fr/" target="_blank" rel="noreferrer noopener" >GUARDA HOTEL</a></span> (LIEU DU MARIAGE)<br></br><span className="indication">NOUS CONTACTER DIRECTEMENT</span></div>
              <br></br>
              <p className="date-title"><span className="link-hotel"><a href="https://pasdelours.ch/fr/" target="_blank" rel="noreferrer noopener" >LE PAS DE L'OURS</a></span></p>
              <p className="date-title"><span className="link-hotel"><a href="https://pasdelours.ch/fr/" target="_blank" rel="noreferrer noopener" >L'AMBASSADOR</a></span></p>
              <p className="date-title"><span className="link-hotel"><a href="https://pasdelours.ch/fr/" target="_blank" rel="noreferrer noopener" >L'HOTEL ROYAL</a></span></p>
            </div>
              <h5 className={this.state.racletteTitle}><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img></h5>
              <button className={this.state.racletteBtn}>
                <span>
                  <span>
                    <span data-attr-span={this.state.racletteBtnTitle}>
                      {this.state.racletteBtnTitle}
                    </span>
                  </span>
                </span>
              </button>
            </div>
          </div>
        </div>
      <div className="col-sm wedding-card mr-4 mb-4">
        <div class="centerBox" onMouseOver={this.overCeremo} onClick={this.showInfoCere} onMouseOut={this.overCeremo}>
          <img className={this.state.ceremofx}
            src={four}  
            alt={four} ></img>
            <div class="categoryWrapper">
              <h5 className="bgbanner-text"><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img></h5>
              <button>
                <span>
                  <span>
                    <span data-attr-span="AFFICHER">
                      AFFICHER
                    </span>
                  </span>
                </span>
              </button>
            </div>
        </div>
      </div>
      <div className="col-sm wedding-card mr-4 mb-4">
      <div class="centerBox" onMouseOver={this.overSoiree} onClick={this.showInfoSoiree} onMouseOut={this.overSoiree}>
      <img className={this.state.soireefx}
          src={three}
          alt={three}></img>
          <div class="categoryWrapper">
              <h5 className="bgbanner-text"><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img><img src={flocon} alt={flocon}></img></h5>
              <button>
                <span>
                  <span>
                    <span data-attr-span="AFFICHER">
                      AFFICHER
                    </span>
                  </span>
                </span>
              </button>  
          </div>
        </div>
      </div>
    </div>
);
  }
}