import React from "react";
import "./../App.css";
import "./../Cards.css";

import welcome from "../static/img/welcome.jpeg";
import ceremonie from "../static/img/ceremonie.jpeg";
import diner from "../static/img/soiree.png";

export default class Cards extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      raclettefx: "bgimg",
      raclettebool: false,
      ceremofx: "bgimg",
      ceremobool: false,
      soireefx: "bgimg",
      soireebool: false,
      rowDisplay : "row normal-display",
      racletteShow: false,
      racletteStat: "raclette-content raclette-hide",
      racletteTitle : "bgbanner-text",
      racletteBtn: "",
      racletteBtnTitle: "AFFICHER",
      racletteBg: "",
      ceremoShow: false, 
      ceremoStat: "raclette-content raclette-hide",
      ceremoTitle: "bgbanner-text",
      ceremoBtn: "",
      ceremoBtnTitle: "AFFICHER",
      ceremoBg: "",
      soireeShow: false, 
      soireeStat: "raclette-content raclette-hide",
      soireeTitle: "bgbanner-text",
      soireeBtn: "",
      soireeBtnTitle: "AFFICHER",
      soireeBg: "",
    };
    this.overRaclette = this.overRaclette.bind(this);
    this.overCeremo = this.overCeremo.bind(this);
    this.overSoiree = this.overSoiree.bind(this);
    this.showInfoRaclette = this.showInfoRaclette.bind(this);
    this.showInfoCere = this.showInfoCere.bind(this);
    this.showInfoSoiree = this.showInfoSoiree.bind(this);
  }
  overRaclette() {
    var val1 = "";
    var val2 = "";
    // if it is hover
    if (this.state.raclettebool === false) {
      // create hover effect
      val1 = "bgimg active";
      val2 = true;
    }
    // if mouse leaves
    else {
      // remove fx
      val1 = "bgimg";
      val2 = false;
      // if raclette is show on then
      console.log(this.state.racletteShow);
    }
    this.setState({
      raclettefx: val1,
      raclettebool: val2,
    });
    if (val2 === true && this.state.racletteShow === true) {
      this.setState({
        racletteBg: ""
      });
    }
    else if (val2 === false && this.state.racletteShow === false) {
      this.setState({
        racletteBg: ""
      });
    }
    else if ((val2 === false && this.state.racletteShow === true)){
      this.setState({
        racletteBg: "active"
      });
    }
  }
  overCeremo() {
    var val1 = "";
    var val2 = "";
    // if it is hover
    if (this.state.ceremobool === false) {
      // create hover effect
      val1 = "bgimg active";
      val2 = true;
    }
    // if mouse leaves
    else {
      // remove fx
      val1 = "bgimg";
      val2 = false;
      // if raclette is show on then
      console.log(this.state.ceremoShow);
    }
    this.setState({
      ceremofx: val1,
      ceremobool: val2,
    });
    if (val2 === true && this.state.ceremoShow === true) {
      this.setState({
        ceremoBg: ""
      });
    }
    else if (val2 === false && this.state.ceremoShow === false) {
      this.setState({
        ceremoBg: ""
      });
    }
    else if ((val2 === false && this.state.ceremoShow === true)){
      this.setState({
        ceremoBg: "active"
      });
    }
  }
  overSoiree() {
    var val1 = "";
    var val2 = "";
    // if it is hover
    if (this.state.soireebool === false) {
      // create hover effect
      val1 = "bgimg active";
      val2 = true;
    }
    // if mouse leaves
    else {
      // remove fx
      val1 = "bgimg";
      val2 = false;
      // if raclette is show on then
      console.log(this.state.soireeShow);
    }
    this.setState({
      soireefx: val1,
      soireebool: val2,
    });
    if (val2 === true && this.state.soireeShow === true) {
      this.setState({
        soireeBg: ""
      });
    }
    else if (val2 === false && this.state.soireeShow === false) {
      this.setState({
        soireeBg: ""
      });
    }
    else if ((val2 === false && this.state.soireeShow === true)){
      this.setState({
        soireeBg: "active"
      });
    }
  }
  showInfoRaclette() {
    if (this.state.racletteShow === false) {
      this.setState({
        racletteShow: true,
        racletteTitle: "bgbanner-text rmoveup",
        racletteBtn: "rmovedown",
        racletteBtnTitle: "FERMER",
      })
      setTimeout(() => { this.setState({
        racletteStat: "raclette-content raclette-show",
      }) }, 500);
    }
    else {
      this.setState({
        racletteShow: false,
        racletteStat: "raclette-content raclette-hide",
        racletteTitle: "bgbanner-text",
        racletteBtn: "",
        racletteBtnTitle: "AFFICHER",
      })
    }
    

  }
  showInfoCere() {
    if (this.state.ceremoShow === false) {
      this.setState({
        ceremoShow: true,
        ceremoTitle: "bgbanner-text rmoveup",
        ceremoBtn: "rmovedown",
        ceremoBtnTitle: "FERMER",
      })
      setTimeout(() => { this.setState({
        ceremoStat: "raclette-content raclette-show",
      }) }, 500);
    }
    else {
      this.setState({
        ceremoShow: false,
        ceremoStat: "raclette-content raclette-hide",
        ceremoTitle: "bgbanner-text",
        ceremoBtn: "",
        ceremoBtnTitle: "AFFICHER",
      })
    }
  }
  showInfoSoiree() {
    if (this.state.soireeShow === false) {
      this.setState({
        soireeShow: true,
        soireeTitle: "bgbanner-text rmoveup",
        soireeBtn: "rmovedown",
        soireeBtnTitle: "FERMER",
      })
      setTimeout(() => { this.setState({
        soireeStat: "raclette-content raclette-show",
      }) }, 500);
    }
    else {
      this.setState({
        soireeShow: false,
        soireeStat: "raclette-content raclette-hide",
        soireeTitle: "bgbanner-text",
        soireeBtn: "",
        soireeBtnTitle: "AFFICHER",
      })
    }
  }
  render() {
    return (
      <div className="row">
        <div className="col-sm wedding-card mr-4 mb-4">
          <div class="centerBox" onMouseOver={this.overRaclette} onClick={this.showInfoRaclette} onMouseOut={this.overRaclette}>
            <img className={`${this.state.raclettefx} ${this.state.racletteBg}`}
            src={welcome} 
            alt={welcome}></img>
            <div class="categoryWrapper">
            <div className={`${this.state.racletteStat}`}>
              <p className="date-title">VENDREDI 18 MARS 2022 A 18H00</p>
              <p className="location-text">A CHETZERON</p>
              <div className="normal-text">
                  WELCOME DINNER
                  AU RESTAURANT CHETZERON
                  (2112 M D'ALTITUDE)
                  RACLETTE PARTY
              </div>
              <p className="normal-text">LES TELECABINES FONCTIONNERONT EXCEPTIONNELLEMENT DE 18 A 19 HEURES PRECISES. APRES 19 HEURES, LA MONTEE N'EST PLUS POSSIBLE</p>
              <p className="normal-text">(PREVOIR SKI ET PEAU DE PHOQUE SI VOUS SOUHAITEZ MONTER APRES 19 HEURES - TEMPS ESTIME : 2 HEURES</p>
              <div>A L'ARRIVEE A CRY D'ER : TRANSFERT EN CHENILLETTES JUSQU'A CHETZERON (PREVOIR UNE SECONDE PAIRE DE CHAUSSURES POUR LES ADEPTES DES TALONS)</div>
              <p className="normal-text">ATTENTION : LES TELECABINES S'ARRETENT A 19H. AU DELA, IL NE SERA PLUS POSSIBLE D'ACCEDER A CHETZERON</p>
              <p>POUR LES INVITES RESIDENT A L'HOTEL GUARDA GOLF, DES NAVETTES SERONT A LEUR DISPOSITION A PARTIR DE 18 HEURES</p>
            </div>
              <h5 className={this.state.racletteTitle}>WELCOME DINNER</h5>
              <button className={this.state.racletteBtn}>
                <span>
                  <span>
                    <span data-attr-span={this.state.racletteBtnTitle}>
                      {this.state.racletteBtnTitle}
                    </span>
                  </span>
                </span>
              </button>
            </div>
          </div>
        </div>
      <div className="col-sm wedding-card mr-4 mb-4">
        <div class="centerBox" onMouseOver={this.overCeremo} onClick={this.showInfoCere} onMouseOut={this.overCeremo}>
          <img className={`${this.state.ceremofx} ${this.state.ceremoBg}`}
            src={ceremonie}  
            alt={ceremonie} ></img>
            <div class="categoryWrapper">
            <div className={`${this.state.ceremoStat}`}>
              <p className="date-title">VENDREDI 18 MARS 2022 A 18H00</p>
              <p className="location-text">A CHETZERON</p>
              <div className="normal-text">
                  WELCOME DINNER
                  AU RESTAURANT CHETZERON
                  (2112 M D'ALTITUDE)
                  RACLETTE PARTY
              </div>
            </div>
              <h5 className={this.state.ceremoTitle}>CEREMONIE RELIGIEUSE</h5>
              <button className={this.state.ceremoBtn}>
                <span>
                  <span>
                    <span data-attr-span={this.state.ceremoBtnTitle}>
                      {this.state.ceremoBtnTitle}
                    </span>
                  </span>
                </span>
              </button>
            </div>
        </div>
      </div>
      <div className="col-sm wedding-card mr-4 mb-4">
        <div class="centerBox" onMouseOver={this.overSoiree} onClick={this.showInfoSoiree} onMouseOut={this.overSoiree}>
        <img className={`${this.state.soireefx} ${this.state.soireeBg}`}
            src={diner}  
            alt={diner} ></img>
            <div class="categoryWrapper">
            <div className={`${this.state.soireeStat}`}>
              <p className="date-title">VENDREDI 18 MARS 2022 A 18H00</p>
              <p className="location-text">A CHETZERON</p>
              <div className="normal-text">
                  WELCOME DINNER
                  AU RESTAURANT CHETZERON
                  (2112 M D'ALTITUDE)
                  RACLETTE PARTY
              </div>
            </div>
              <h5 className={this.state.soireeTitle}>SOIREE</h5>
              <button className={this.state.soireeBtn}>
                <span>
                  <span>
                    <span data-attr-span={this.state.soireeBtnTitle}>
                      {this.state.soireeBtnTitle}
                    </span>
                  </span>
                </span>
              </button>
            </div>
        </div>
      </div>
    </div>
);
  }
}